/**
 * CONTEXTO DE AUTENTICAO
 * 
 * Este contexto gerencia todo o estado de autenticao da aplicao,
 * incluindo login, logout, registro e persistncia de dados do usurio.
 * 
 * Funcionalidades:
 * - Gerenciamento de estado do usurio logado
 * - Persistncia de dados no localStorage
 * - Redirecionamento automtico aps login/logout
 * - Validao de autenticao
 */

import React, { createContext, useContext, useState, useEffect, ReactNode, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { authService } from '../services/authService';
import { LoginData, RegisterData, User, AuthResponse } from '../types/Auth';

/**
 * Interface que define o tipo do contexto de autenticao
 * Contm todas as funes e estados relacionados  autenticao
 */
interface AuthContextType {
  user: User | null;                    // Dados do usurio logado (null se no autenticado)
  token: string | null;                 // Token JWT atual (null se no autenticado)
  login: (credentials: LoginData) => Promise<void>;        // Funo para fazer login
  register: (userData: RegisterData) => Promise<void>;    // Funo para registrar usurio
  logout: () => void;                   // Funo para fazer logout
  isAuthenticated: boolean;             // Indica se o usurio est autenticado
  isLoading: boolean;                   // Indica se est carregando (login/registro)
}

// Criao do contexto de autenticao
const AuthContext = createContext<AuthContextType | undefined>(undefined);

/**
 * Props do componente AuthProvider
 */
interface AuthProviderProps {
  children: ReactNode;
}

/**
 * Provider do contexto de autenticao
 * Gerencia todo o estado de autenticao da aplicao
 */
export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  // Estados do contexto
  const [user, setUser] = useState<User | null>(null);           // Usurio atual
  const [token, setToken] = useState<string | null>(null);        // Token atual
  const [isLoading, setIsLoading] = useState(true);              // Estado de carregamento
  const navigate = useNavigate();                                 // Hook para navegao

  /**
   * Efeito para verificar autenticao na inicializao
   * Restaura dados do localStorage ao recarregar a pgina
   */
  useEffect(() => {
    const storedToken = localStorage.getItem('token');
    const storedUser = localStorage.getItem('user');
    
    // Se existem dados salvos, restaurar o estado de autenticao
    if (storedToken && storedUser) {
      setToken(storedToken);
      setUser(JSON.parse(storedUser));
    }
    
    setIsLoading(false); // Finalizar carregamento inicial
  }, []);

  /**
   * Funo para realizar login do usurio
   * Autentica o usurio e salva os dados no localStorage
   */
  const login = useCallback(async (credentials: LoginData) => {
    setIsLoading(true);
    try {
      // Chamar servio de autenticao
      const response: AuthResponse = await authService.login(credentials);
      
      // Atualizar estados locais PRIMEIRO
      setToken(response.token);
      
      // Salvar token no localStorage ANTES de buscar dados do usurio
      localStorage.setItem('token', response.token);
      
      // Buscar dados completos do usurio (agora com token salvo)
      const userData = await authService.getCurrentUser();
      
      // Atualizar estado do usurio
      setUser(userData);
      
      // Salvar dados completos do usurio no localStorage
      localStorage.setItem('user', JSON.stringify(userData));
      
      // Redirecionar para a pgina inicial aps login bem-sucedido
      navigate('/');
    } catch (error) {
      console.error('Erro no login:', error);
      throw error; // Propagar o erro para o componente que chamou
    } finally {
      setIsLoading(false);
    }
  }, [navigate]);

  /**
   * Funo para registrar novo usurio
   * Cria uma nova conta e redireciona para login
   */
  const register = useCallback(async (userData: RegisterData) => {
    setIsLoading(true);
    try {
      // Chamar servio de registro
      await authService.register(userData);
      
      // Aps registro bem-sucedido, redirecionar para tela de login
      navigate('/login');
    } catch (error) {
      console.error('Erro no registro:', error);
      throw error; // Propagar o erro para o componente que chamou
    } finally {
      setIsLoading(false);
    }
  }, [navigate]);

  /**
   * Funo para fazer logout do usurio
   * Limpa todos os dados de autenticao e redireciona para login
   */
  const logout = useCallback(() => {
    // Limpar estados locais
    setToken(null);
    setUser(null);
    
    // Remover dados do localStorage
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    
    // Redirecionar para pgina de login
    navigate('/login');
  }, [navigate]);

  // Calcular se o usurio est autenticado
  const isAuthenticated = !!token && !!user;

  // Valor do contexto com todas as funes e estados
  const value = {
    user,
    token,
    login,
    register,
    logout,
    isAuthenticated,
    isLoading
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

/**
 * Hook personalizado para usar o contexto de autenticao
 * Garante que o contexto seja usado apenas dentro do AuthProvider
 */
export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth deve ser usado dentro de um AuthProvider');
  }
  return context;
};
