/**
 * SERVIO DE ADMINISTRAO
 * 
 * Fornece mtodos para operaes administrativas,
 * incluindo aprovao e gerenciamento de usurios.
 */

import api from './api';
import { PendingUser } from '../types/Auth';

/**
 * Servio para operaes administrativas
 */
export const adminService = {
  /**
   * Listar usurios aguardando aprovao
   * @returns Promise<PendingUser[]> - Lista de usurios pendentes
   */
  getPendingUsers: async (): Promise<PendingUser[]> => {
    const response = await api.get('/admin/users/pending');
    return response.data;
  },

  /**
   * Aprovar usurio
   * @param userId - ID do usurio a ser aprovado
   * @returns Promise<{ message: string; user: any }> - Resposta da aprovao
   */
  approveUser: async (userId: string): Promise<{ message: string; user: any }> => {
    const response = await api.post(`/admin/users/${userId}/approve`);
    return response.data;
  },

  /**
   * Rejeitar usurio
   * @param userId - ID do usurio a ser rejeitado
   * @returns Promise<{ message: string; user: any }> - Resposta da rejeio
   */
  rejectUser: async (userId: string): Promise<{ message: string; user: any }> => {
    const response = await api.post(`/admin/users/${userId}/reject`);
    return response.data;
  },

  /**
   * Listar todos os usurios (apenas administradores)
   * @returns Promise<any[]> - Lista completa de usurios
   */
  getAllUsers: async (): Promise<any[]> => {
    const response = await api.get('/admin/users');
    return response.data;
  },

  /**
   * Bloquear usurio temporariamente
   * @param userId - ID do usurio a ser bloqueado
   * @param reason - Motivo do bloqueio
   * @returns Promise<{ message: string; user: any }> - Resposta do bloqueio
   */
  blockUser: async (userId: string, reason?: string): Promise<{ message: string; user: any }> => {
    const response = await api.post(`/admin/users/${userId}/block`, { reason });
    return response.data;
  },

  /**
   * Desbloquear usurio
   * @param userId - ID do usurio a ser desbloqueado
   * @returns Promise<{ message: string; user: any }> - Resposta do desbloqueio
   */
  unblockUser: async (userId: string): Promise<{ message: string; user: any }> => {
    const response = await api.post(`/admin/users/${userId}/unblock`);
    return response.data;
  },

  /**
   * Deletar usurio permanentemente
   * @param userId - ID do usurio a ser deletado
   * @returns Promise<{ message: string; user: any }> - Resposta da deleo
   */
  deleteUser: async (userId: string): Promise<{ message: string; user: any }> => {
    const response = await api.delete(`/admin/users/${userId}`);
    return response.data;
  },
};
