/**
 * SERVIO DE AUTENTICAO
 * 
 * Fornece mtodos para autenticao de usurios, incluindo
 * login e registro de novas contas.
 */

import api from './api';
import { LoginData, RegisterData, AuthResponse, User } from '../types/Auth';

/**
 * Servio para operaes de autenticao
 */
export const authService = {
  /**
   * Realizar login do usurio
   * @param credentials - Dados de login (username e password)
   * @returns Promise<AuthResponse> - Token e dados do usurio
   */
  login: async (credentials: LoginData): Promise<AuthResponse> => {
    const response = await api.post('/login', credentials);
    return response.data;
  },

  /**
   * Registrar novo usurio
   * @param userData - Dados de registro (username e password)
   * @returns Promise<{ message: string }> - Mensagem de confirmao
   */
  register: async (userData: RegisterData): Promise<{ message: string }> => {
    const response = await api.post('/register', userData);
    return response.data;
  },

  /**
   * Buscar dados completos do usurio logado
   * @returns Promise<User> - Dados completos do usurio
   */
  getCurrentUser: async (): Promise<User> => {
    const response = await api.get('/users/me');
    return response.data;
  },
};
