/**
 * CONTEXTO DE CONFIGURAES
 * 
 * Gerencia as configuraes globais da aplicao, incluindo:
 * - Tema (claro/escuro)
 * - Margem de lucro configurvel
 * - Sincronizao com backend e localStorage
 */

import React, { createContext, useContext, useState, useEffect, ReactNode, useCallback } from 'react';
import { settingsService } from '../services/settingsService';

/**
 * Interface para configuraes da aplicao
 */
interface AppSettings {
  theme: 'light' | 'dark';    // Tema da interface
  profitMargin: number;       // Margem de lucro em decimal (ex: 0.5 = 50%)
}

/**
 * Interface do contexto de configuraes
 */
interface SettingsContextType {
  settings: AppSettings;                                    // Configuraes atuais
  toggleTheme: () => void;                                  // Alternar tema
  updateProfitMargin: (margin: number) => Promise<void>;    // Atualizar margem de lucro
  loadSettings: () => Promise<void>;                         // Carregar configuraes do backend
}

// Configuraes padro
const defaultSettings: AppSettings = {
  theme: 'light',
  profitMargin: 0.5 // 50% de margem de lucro padro
};

const SettingsContext = createContext<SettingsContextType | undefined>(undefined);

interface SettingsProviderProps {
  children: ReactNode;
}

/**
 * Provider do contexto de configuraes
 * Gerencia estado das configuraes e sincronizao com backend/localStorage
 */
export const SettingsProvider: React.FC<SettingsProviderProps> = ({ children }) => {
  const [settings, setSettings] = useState<AppSettings>(() => {
    // Carregar configuraes do localStorage na inicializao
    const savedSettings = localStorage.getItem('appSettings');
    const initialSettings = savedSettings ? JSON.parse(savedSettings) : defaultSettings;
    
    return {
      ...initialSettings,
      profitMargin: initialSettings.profitMargin !== undefined ? initialSettings.profitMargin : defaultSettings.profitMargin
    };
  });

  /**
   * Carregar margem de lucro do backend
   * Sincroniza com as configuraes do servidor
   */
  const loadSettings = useCallback(async () => {
    try {
      const data = await settingsService.getProfitMargin();
      setSettings(prev => ({ ...prev, profitMargin: data.profitMargin }));
    } catch (error) {
      console.error('Erro ao carregar margem de lucro do backend:', error);
    }
  }, []);

  // Carregar configuraes do backend na inicializao
  useEffect(() => {
    loadSettings();
  }, [loadSettings]);

  // Salvar tema no localStorage sempre que for alterado
  useEffect(() => {
    localStorage.setItem('appSettings', JSON.stringify({ theme: settings.theme }));
  }, [settings.theme]);

  /**
   * Alternar entre tema claro e escuro
   */
  const toggleTheme = useCallback(() => {
    setSettings(prev => ({
      ...prev,
      theme: prev.theme === 'light' ? 'dark' : 'light'
    }));
  }, []);

  /**
   * Atualizar margem de lucro no backend
   * Sincroniza a mudana com o servidor
   */
  const updateProfitMargin = useCallback(async (margin: number) => {
    try {
      const data = await settingsService.updateProfitMargin({ newProfitMargin: margin });
      setSettings(prev => ({ ...prev, profitMargin: data.profitMargin }));
    } catch (error) {
      console.error('Erro ao atualizar margem de lucro no backend:', error);
    }
  }, []);

  const value: SettingsContextType = {
    settings,
    toggleTheme,
    updateProfitMargin,
    loadSettings
  };

  return (
    <SettingsContext.Provider value={value}>
      {children}
    </SettingsContext.Provider>
  );
};

/**
 * Hook para usar o contexto de configuraes
 */
export const useSettings = (): SettingsContextType => {
  const context = useContext(SettingsContext);
  if (context === undefined) {
    throw new Error('useSettings deve ser usado dentro de um SettingsProvider');
  }
  return context;
};
